import { Injectable } from '@angular/core';
import { Observable} from 'rxjs';
import { ApiRequestService } from '../api-request.service';
import {AssociateEnpoints } from './associate-enpoints';

@Injectable()
export class AssociateRpInstancesService {

  constructor(private apiRequest: ApiRequestService, private endpoints: AssociateEnpoints) { }

  public getRpInstances(): Observable<any[]>{
    return this.apiRequest.get(this.endpoints.getRpInstancesEndpoint());
  }

}



