import { Component, OnInit, AfterViewInit, ElementRef } from '@angular/core';
import { swaggerUIBundle, swaggerUIStandalonePreset } from '../../../../polyfills';
import {AssociateEnpoints } from '../../../services/associate/associate-enpoints';

@Component({
  selector: 'app-associate-endpoints',
  templateUrl: './associate-endpoints.component.html',
  styleUrls: ['./associate-endpoints.component.css']
})
export class AssociateEndpointsComponent implements OnInit, AfterViewInit {

  constructor(private el: ElementRef,
              private endpoints : AssociateEnpoints) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    const ui = swaggerUIBundle({
        urls: [{
            validatorUrl : null,
            url: this.endpoints.getSwaggerJsonEndpoint()
                    , name: 'associate-interface'
        }],
        domNode: this.el.nativeElement.querySelector('.swagger-container'),
        deepLinking: true,
        presets: [
            swaggerUIBundle.presets.apis,
            swaggerUIStandalonePreset
        ],
        plugins: [
            swaggerUIBundle.plugins.DownloadUrl
        ],
        layout: 'StandaloneLayout'
    });
}


}



