import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse} from '@angular/common/http';
import { Observable} from 'rxjs';
import { ApiRequestService } from '../api-request.service';
import { TorEndpoints } from './tor-endpoints'
import { ServiceError } from '../../models/service-error';

import { CalendarEventAction } from 'angular-calendar';
import { format, addDays, addHours } from 'date-fns';
import { UtcDatePipe } from '../../directives/utc-date.pipe';
//models
import { Maintenance } from '../../models/maintenance';
import { WeekDays } from '../../models/week-days.enum';
import { MaintenanceDay, MaintenanceDayBuilder } from '../../models/maintenance-builder';

@Injectable()
export class MaintenanceService {

  

  constructor(private apiRequest: ApiRequestService, private endpoints: TorEndpoints) {
  }

  //trasnform maintenance to weekday bootstrap component
  public assignMaintenance(maintenances: Maintenance[], actions: CalendarEventAction[]) {
    let events = [];
    for (let maintenance of maintenances) {
      events.push(new MaintenanceDayBuilder().buildMaintenanceDayView(maintenance, actions).event);
    }
    return events;
  }

  //obtain all days of week
  public getWeekDays(): string[] {
    let dias: string[];
    dias = [];
    let numbers = [1, 2, 3, 4, 5, 6, 7];
    for (let num of numbers) {
      dias.push(WeekDays[num]);
    }
    return dias;
  }

  //create errors for the view
  public createError(message: string, body: string): ServiceError {
    return new ServiceError(message,JSON.stringify(body));
  }

  //service to obtain maintenances by  market
  public getMaintenanceByMarket(market: string): Observable<any> {
    return this.apiRequest.get(this.endpoints.getMaintenanceByMarket(market));
  }

  //update or create maintenance
  public upsertMaintenance(maintenance: Maintenance): Observable<any> {
    return this.apiRequest.post(this.endpoints.upsertMaintenance(), maintenance);
  }

  //delete maintenance
  public deletetMaintenance(maintenance: Maintenance): Observable<any> {
    return this.apiRequest.post(this.endpoints.deleteMaintenance(), new MaintenanceDayBuilder().buildMaintenanceDayDelete(maintenance).maintenance);
  }
}



