import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest,HttpHeaders,HttpErrorResponse,HttpParams} from '@angular/common/http';
import { Observable} from 'rxjs';
import { ApiRequestService } from '../api-request.service';
import { TorEndpoints } from './tor-endpoints';
import { ServiceError } from '../../models/service-error';
import { Message } from '../../models/message';
import { Event } from '../../models/event';



@Injectable()
export class TorService {

  private static readonly dateFormatElastic = 'YYYY-MM-DDTHH:mm:ss';
  private static readonly elasticIndex = 'events';
  

  constructor(private endpoints: TorEndpoints, 
              private apiRequest: ApiRequestService) { }

  public getEventsByIdAndDate(id: string, startDate: Date, endDate: Date): Observable<any> {
    let  DAY_CONSTANT = 3600*24*1000;
    let elasticId=(id==null || id==undefined)?"":id;
    let start = startDate.getTime();
    let end =endDate.getTime()+DAY_CONSTANT;
    let endpoint: string = this.endpoints.getEventsByIdOrDate(elasticId,start,end);
    let params = new HttpParams().set("id",id).set("initDate",start+"").set("endDate",end+"");
    return this.apiRequest.get(this.endpoints.getEventsByIdOrDate(elasticId,start,end),params);
  }

  public reprocesTor(event: Event):Observable<any>{
    let idElastic:string=event.message.customObject.market+event.message.customObject.requestIdGta+event.message.customObject.status;
    console.info('Reprocessing : '+idElastic);
    return this.apiRequest.post(this.endpoints.reprocessTor(),idElastic);
  }

  public reprocesTors(events: string[]):Observable<any>{
    return this.apiRequest.post(this.endpoints.reprocessTors(),events);
  }

  public deleteEvent(event:Event):Observable<any>{
    return this.apiRequest.delete(this.endpoints.deleteEvent(event.id,TorService.elasticIndex));
  }
}


