import { Injectable, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, ReplaySubject, Subject} from 'rxjs';
import { UserInfoService, LoginInfoInStorage} from './api/user-info.service';
import { ServiceError } from '../models/service-error';
import { HttpClient, HttpRequest, HttpHeaders,HttpParams, HttpErrorResponse} from '@angular/common/http';
import { catchError } from 'rxjs/operators';


@Injectable()
export class ApiRequestService {

    private headers:Headers;
    private BASE:string =".."

    constructor(

        private http: HttpClient,
        private router:Router,
        private userInfoService:UserInfoService
    ) {}

    /**
     * This is a Global place to add all the request headers for every REST calls
     */
    appendAuthHeader():HttpHeaders {
        let token = this.userInfoService.getStoredToken();
        if (token !==null) {
            const headers : HttpHeaders = new HttpHeaders()
                        .append('Content-Type','application/json')
                        .append('Authorization', token);
            return headers;
        }
    }

    /**
     * This is a Global place to define all the Request Headers that must be sent for every ajax call
     */
    getRequestOptions(requestMethod:string, url:string, urlParam?:HttpParams, body?:Object) :HttpRequest<any> {
        let options={
            headers:this.appendAuthHeader(),
            params:urlParam,
            body:body,
            responseType: 'json'
        }

        console.log('Requesting:',options)
        let request= new HttpRequest(requestMethod,url,options);      
        return request;
    }

    get(url:string, urlParams?:HttpParams){
        let me = this;
        const opts={
            headers: this.appendAuthHeader(),
            params:urlParams
            }
        return this.http.get(this.BASE+url,opts)
        .map(resp => resp)
        .catch(function(error:HttpErrorResponse){
            if (error.status === 403 || error.status == 401){
                me.router.navigate(['/logout']);
            }
           return me.errorHandler(error);
        });
    }

    post(url:string, body:Object):Observable<any>{
        let me = this;
        const opts={headers: this.appendAuthHeader()}
        return this.http.post(this.BASE+url,body,opts)
            .map(resp => resp)
            .catch(function(error:HttpErrorResponse){
                if (error.status === 403 || error.status == 401){
                    me.router.navigate(['/logout']);
                }
                return me.errorHandler(error);
            });
    }

    put(url:string, body:Object):Observable<any>{
        let me = this;
        const opts={headers: this.appendAuthHeader()}
        return this.http.put(this.BASE+url,body,opts)
        .map(resp => resp)
        .catch(function(error:HttpErrorResponse){
            if (error.status === 403 || error.status == 401){
                me.router.navigate(['/logout']);
            }
            return me.errorHandler(error);
        });
    }

    delete(url:string,urlParams?:HttpParams):Observable<any>{
        let me = this;
        const opts={headers: this.appendAuthHeader()}
        return this.http.delete(this.BASE+url,opts)
        .map(resp => resp)
        .catch(function(error:HttpErrorResponse){
            if (error.status === 403 || error.status == 401){
                me.router.navigate(['/logout']);
            }
            return me.errorHandler(error);
        });
    }

    errorHandler(error: HttpErrorResponse){
        if (error.error.message!= undefined) {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
             return Observable.throw(new ServiceError(error.error.message,error.error.developerMessage));
          } else {
            // A client-side or network error occurred. Handle it accordingly.
             return Observable.throw(new ServiceError(error.statusText,error.message));
          }
    }

    

}


