import {Component, Input, OnChanges,SimpleChange, Optional} from "@angular/core";
import {WfmTableDirective, PageEvent} from "./wfm-table.directive";
import * as _ from "lodash";

@Component({
    selector: "wfmPaginator",
    template: `
        <div class="clearfix">
           
            <div class="float-left ">
            <strong class="small" for="pagenumber">Page Registers.	&nbsp;	&nbsp;</strong>
            </div>
            <div class="float-left">
                <select id="pages float-left" class="rounded-0" (change)="setRowsOnPage($event.target.value)">
                    <option *ngFor="let rows of rowsOnPageSet">{{rows}}</option>
                </select>
                
            </div>
            <div class="float-left">
            <strong class="small"> &nbsp; &nbsp;{{wfmTable.inputData.length}}&nbsp; total records </strong>
            </div>
            <div class="float-right" *ngIf="dataLength > rowsOnPage">
            <a  class="text-primary"  *ngIf="activePage > 2" (click)="setPage(1)" >            <i class="fa fa-fast-backward"></i></a>
            <a  class="text-primary"  *ngIf="activePage > 1" (click)="setPage(activePage - 1)"><i class="fa fa-backward"></i></a>
            <a  class="text-primary"  *ngIf="activePage > 4 && activePage + 1 > lastPage" (click)="setPage(activePage - 4)">{{activePage-4}}</a>
            <a  class="text-primary"  *ngIf="activePage > 3 && activePage + 2 > lastPage" (click)="setPage(activePage - 3)">{{activePage-3}}</a>
            <a  class="text-primary"  *ngIf="activePage > 2" (click)="setPage(activePage - 2)">{{activePage-2}}</a>
            <a  class="text-primary"  *ngIf="activePage > 1" (click)="setPage(activePage - 1)">{{activePage-1}}</a>
            <a  (click)="setPage(activePage)">    {{activePage}}</a>
            <a  class="text-primary"  *ngIf="activePage + 1 < lastPage" (click)="setPage(activePage+1)">  {{activePage+1}}</a>
            <a  class="text-primary"  *ngIf="activePage + 2 < lastPage" (click)="setPage(activePage+2)">  {{activePage+2}}</a>
            <a  class="text-primary"  *ngIf="activePage + 3 < lastPage && activePage < 3" (click)="setPage(activePage+3)">  {{activePage+3}}</a>
            <a  class="text-primary"  *ngIf="activePage + 4 < lastPage && activePage < 2" (click)="setPage(activePage+4)">  {{activePage+4}}</a>
            <a  class="text-primary"  *ngIf="activePage + 1 < lastPage " (click)="setPage(activePage+1)">  <i class="fa fa-forward"></i></a>
            <a  class="text-primary"  *ngIf="activePage < lastPage" (click)="setPage(lastPage)">      <i class="fa fa-fast-forward"></i></a>
            </div>
            
        </div>
    `
})
export class WfmPaginator implements OnChanges {
    @Input("rowsOnPageSet") rowsOnPageSet = [];

    @Input("wmfTable") inputTable: WfmTableDirective;
    public wfmTable: WfmTableDirective;
    public activePage: number;
    public rowsOnPage: number;
    public dataLength: number = 0;
    public lastPage: number;

    public minRowsOnPage = 0;

    public constructor(@Optional() private injectTable: WfmTableDirective) {
    }

   /* ngOnChanges(changes: any): any {
       
        if(changes.)
    }*/

    public ngOnChanges(changes: {[key: string]: SimpleChange}): any {
        this.wfmTable = this.inputTable || this.injectTable;
        console.info('WfmPaginator : ngOnChanges');
        this.onPageChangeSubscriber(this.wfmTable.getPage());
        this.wfmTable.onPageChange.subscribe(this.onPageChangeSubscriber);

        if (changes.rowsOnPageSet) {
            this.minRowsOnPage = _.min(this.rowsOnPageSet)
        }
    }

    public setPage(pageNumber: number): void {
        console.log('setting page:',pageNumber);
        this.wfmTable.setPage(pageNumber, this.rowsOnPage);
    }

    public setRowsOnPage(rowsOnPage: number): void {
        this.wfmTable.setPage(this.activePage, rowsOnPage);
    }

    private onPageChangeSubscriber = (event: PageEvent)=> {
        this.activePage = event.activePage;
        this.rowsOnPage = Number(event.rowsOnPage);
        this.dataLength = event.dataLength;
        this.lastPage = Math.ceil(this.dataLength / this.rowsOnPage);
    };
}



