import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormsModule } from '@angular/forms';

import { Json2Csv } from '../../../commons/json-2-csv';
import { PapaParseService } from 'ngx-papaparse';

import { WfmTableDirective } from '../../../directives/wfm-table.directive';
import { WfmPaginator } from '../../../directives/paginator';
import { DataFilterPipe } from '../../../directives/data-filter.pipe';
import { ServiceError } from '../../../models/service-error';
import { AssociateRpInstanceDto } from '../../../models/associate-rp-instance-dto';
import { AssociateStoreDto } from '../../../models/associate-store-dto';

import { AssociateStoresService } from '../../../services/associate/associate-stores.service';
import { AssociateRpInstancesService } from '../../../services/associate/associate-rp-instances.service';

enum ViewEnum {
  add = 1,
  update = 2,
  load = 3,
}

@Component({
  selector: 'app-associate-stores',
  templateUrl: './associate-stores.component.html',
  styleUrls: ['./associate-stores.component.css']
})
export class AssociateStoresComponent implements OnInit {

  private sub: any;

  public stores: AssociateStoreDto[];
  public currentStore: AssociateStoreDto;
  public rpInstance:string;
  public rpInstances : AssociateRpInstanceDto[];

  public showEdit: boolean;
  public viewId: number;

  public storesError: ServiceError;
  public loadingStores: boolean;

  public closeResult: string;
  public filterQuery = "";
  public rowsOnPage = 5;
  public sortBy = "storeNumber";
  public sortOrder = "asc";

  constructor(private route: ActivatedRoute,
    public storesService: AssociateStoresService,
    public instancesService: AssociateRpInstancesService,
    public papa: PapaParseService
  ) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.rpInstance = params['market']
      this.viewId = 0;
      this.stores = [];
      this.showEdit = false;
      this.loadStores(this.rpInstance);
      this.filterQuery="";
      this.instancesService.getRpInstances().subscribe(
        data => { this.rpInstances = data; this.loadingStores = false; },
        error => { this.storesError = error; this.loadingStores = false; });
    });
  }

  public loadStores(rpInstance: string) {
    this.storesError=undefined;
    this.loadingStores = true;
    this.storesService.getStoresByRpInstance(rpInstance).subscribe(
      stores => { this.stores = stores; this.loadingStores = false; },
      error => { this.storesError = error; this.loadingStores = false; }
    );
  }


  public openEdit(action: string, store: AssociateStoreDto) {
    this.storesError=undefined;
    this.showEdit = true;
    this.viewId = ViewEnum[action];
    if (store == undefined) {
      this.currentStore = new AssociateStoreDto();
      this.currentStore.rpInstance = this.rpInstances.filter((rpInstance)=>rpInstance.rpInstance==this.rpInstance).pop();
    } else {
      this.currentStore = store;
    }

  }

  public closeEdit() {
    this.currentStore = undefined;
    this.showEdit = false;
    this.storesError=undefined;
  }

  public updateStore(){
    this.loadingStores = true;
    this.storesError=undefined;
    this.storesService.upadteStore(this.rpInstance,this.currentStore).subscribe(
      success =>{console.info('succesfull Update');this.loadingStores = false;},
      error => { this.storesError = error; this.loadingStores = false; }
    );
  }

  public addStore() {
    this.loadingStores = true;
    this.storesError=undefined;
    this.storesService.addNewStore(this.rpInstance,this.currentStore).subscribe(
      success =>{console.info('succesfull Update');this.loadingStores = false;
                this.loadStores(this.rpInstance)
                },
      error => { this.storesError = error; this.loadingStores = false; }
    );
  }

  public downloadStoresInScope() {
    let date: Date = new Date();
    new Json2Csv(this.papa.unparse(this.stores), 'AssociateStores-' + date.getFullYear() + (date.getMonth() + 1) + date.getDate());
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



