import { Component, OnInit } from '@angular/core';
import { PapaParseService } from 'ngx-papaparse';
import { Json2Csv } from '../../../commons/json-2-csv';
import { StoresService } from '../../../services/tor/stores.service';
import { ServiceError } from '../../../models/service-error';

@Component({
  selector: 'app-stores-load',
  templateUrl: './stores-load.component.html',
  styleUrls: ['./stores-load.component.css']
})
export class StoresLoadComponent implements OnInit {

  
  public storesToLoad: any[];
  public file :File;
  public filename: string;
  public loadError: ServiceError;
  public loadingStores: boolean;
  public sucessMessage: string;

  public filterQuery = "";
  public rowsOnPage = 5;
  public sortBy = "storeNumber";
  public sortOrder = "asc";
  
  private regexGTA :any;
  private regexStrNumber :any;

  constructor(public papa: PapaParseService,
              public storesServices: StoresService) { 
                this.storesToLoad=[];
                this.file=undefined;
                this.regexGTA=new RegExp('^[A-Z0-9_-]{5,9}$');
                this.regexStrNumber= new RegExp('^[0-9]{3,5}$');
                this.sucessMessage=undefined;
                this.filename='';
              }

  ngOnInit() {
    
  }

  public fileUploadListener($event: any): void {
    this.storesToLoad=[];
    
    this.loadError=undefined;
    this.file=$event.target.files[0];
    this.filename=this.file.name;
    this.papa.parse(this.file, {
      header: true,
      dynamicTyping: false,
      complete: (results: any, file: any) => {
        this.storesToLoad = results.data;
        if(results.data.length==0){
          this.loadError= new ServiceError('Empty file!','The file no contains any store to load.');
        }
        for(let index in results.data){
          if(!this.checkRecord(results.data[index].storeNumber,results.data[index].storeNumberGta)){
            this.loadError= new ServiceError('There are invalid records!', 'Review  if all the storeNumbers contains only numbers and the storeNumberGta has at least 5 digits.');
            break;
          }
        }
      }
    });
  }

  public downloadFile() {
    let sample: any[] = [{
      market: "US",
      storeNumber: 1,
      storeNumberGta: "00001",
      businessUnitId: "000000001",
      storeName: "businesUnit - format- location",

    }];
    new Json2Csv(this.papa.unparse(sample,{header: false}), 'SampleStoresLoad');

  }

  public checkRecord(storeNumber:string,storeNumberGta:string){
    return this.regexGTA.test(storeNumberGta) && this.regexStrNumber.test(storeNumber) ;
  }

  public sendStores() {
    this.sucessMessage=undefined;
    this.loadingStores = true;
    this.storesServices.loadStores(this.file).subscribe(
      result => { this.sucessMessage=result; this.storesToLoad=[]; this.loadingStores = false; },
      error => { this.loadError = error; this.loadingStores = false; }
    );
  }

}



