import { Injectable } from '@angular/core';
import { Observable} from 'rxjs';
import { ApiRequestService } from '../api-request.service';
import { AssociateEnpoints } from './associate-enpoints';
import { AssociateStoreDto } from '../../models/associate-store-dto';

@Injectable()
export class AssociateStoresService {

  constructor(private apiRequest: ApiRequestService, private endpoints: AssociateEnpoints) { }


  public getStoresByRpInstance(rpInstance:string): Observable<AssociateStoreDto[]>{
    return this.apiRequest.get(this.endpoints.getStoresByRpInstanceEndpoint(rpInstance));
  }

  public getStoreByRpInstanceAndStoreNumber(rpInstance:string, storeNumber:number): Observable<AssociateStoreDto>{
    return this.apiRequest.get(this.endpoints.getStoreInfoEndpoint(rpInstance,storeNumber));
  }

  public addNewStore(rpInstance:string,store:AssociateStoreDto):Observable<AssociateStoreDto>{
    let endpoint = this.endpoints.createStoreEndpoint(rpInstance);
    return this.apiRequest.post(endpoint,store);    
  }

  public upadteStore(rpInstance:string,store:AssociateStoreDto):Observable<AssociateStoreDto>{
    let endpoint = this.endpoints.updateStoreEndpoint(rpInstance,store.storeNumber);
    return this.apiRequest.put(endpoint,store);    
  }

}



