import { Injectable, Inject } from '@angular/core';

import { Router } from '@angular/router';

import { Observable,Subject } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { UserInfoService, LoginInfoInStorage} from './user-info.service';
import { ApiRequestService } from '../api-request.service';
import { HttpClient, HttpParams } from '@angular/common/http';

export interface LoginRequestParam{
    username:string;
    password:string;
}

@Injectable()
export class LoginService {

    public landingPage:string = "/tor/dashboard/events";
    constructor(
        private router:Router,
        private http: HttpClient,
        private userInfoService: UserInfoService,
        private apiRequest: ApiRequestService
    ) {}

    private parseAuthUrl(url : string ):any{
        let values:string[] = url.split("?");
        if(values.length>1){
            let queryParams:string[] = values[1].split("&");
            let params = {};
            for (const param in queryParams) {
                let qp:string[] = queryParams[param].split("=");
                params[qp[0]]=qp[1];
            }

        return params;
        }else{
            return null;
        }
    }

    public getToken(url:string): Observable<any> {
        let me = this;
        let params =this. parseAuthUrl(url);
        let loginDataSubject:Subject<any> = new Subject<any>(); // Will use this subject to emit data that we want after ajax login attempt
        let loginInfoReturn:LoginInfoInStorage; // Object that we want to send back to Login Page
        if(params!=null){
        this.apiRequest.post('/token', params)
            .subscribe(jsonResp => {
                if (jsonResp !== undefined && jsonResp !== null && jsonResp.operationStatus === "SUCCESS"){
                    //Create a success object that we want to send back to login page
                    loginInfoReturn = {
                        "success"    : true,
                        "message"    : jsonResp.operationMessage,
                        "landingPage": this.landingPage,
                        "user"       : {
                            "userId"     : jsonResp.item.userId,
                            "email"      : jsonResp.item.emailAddress,
                            "displayName": jsonResp.item.fullName,
                            "token"      : jsonResp.item.token,
                        }
                    };

                    // store username and jwt token in session storage to keep user logged in between page refreshes
                    this.userInfoService.storeUserInfo(JSON.stringify(loginInfoReturn.user));
                }
                else {
                    //Create a faliure object that we want to send back to login page
                    loginInfoReturn = {
                        "success":false,
                        "message":jsonResp.msgDesc,
                        "landingPage":"/logout"
                    };
                    console.error(loginInfoReturn);
                }
                loginDataSubject.next(loginInfoReturn);
            },error=>{
                loginDataSubject.error(error);
            });

            
        }else{
            loginDataSubject.error("Code is not set.");
            this.logout();
        }

        return loginDataSubject;
    }

     public logout(navigatetoLogout=true): void {
        // clear token remove user from local storage to log user out
        this.userInfoService.removeUserInfo();
        if(navigatetoLogout){
            this.router.navigate(["logout"]);
        }
    }
}


