import {NgModule} from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


/// App pages
import { HomeComponent } from './components/home/home.component';
import { TorsComponent } from './pages/tor/tors/tors.component';
import { LogoutComponent } from './pages/logout/logout.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';

import { StoresComponent } from './pages/tor/stores/stores.component';
import { MaintenanceComponent } from './pages/tor/maintenance/maintenance.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { AssociateEndpointsComponent } from './pages/associate/associate-endpoints/associate-endpoints.component';
import { AssociateReprocessComponent } from './pages/associate/associate-reprocess/associate-reprocess.component'
import { AssociateStoresComponent } from './pages/associate/associate-stores/associate-stores.component';


//providers
import { AuthGuardService } from './services/api/auth-guard.service';

const appRoutes: Routes = [
  { path: '', redirectTo: 'tor/dashboard/events', pathMatch: 'full' },
  { path: 'healthchecks',
  component: HomeComponent,
  canActivate:[AuthGuardService],
  children:[
    { path : '', redirectTo: 'healthchecks/dashboard/elasticHC', pathMatch: 'full'},
    { path : 'dashboard/:view', component: DashboardComponent },
  ]},
  { path: 'tor',
    component: HomeComponent,
    canActivate:[AuthGuardService],
    children:[
      { path : '', redirectTo: 'tor/dashboard/events', pathMatch: 'full'},
      { path : 'dashboard/:view', component: DashboardComponent },
      { path : 'reprocess', component: TorsComponent },
      { path : 'stores/:market', component: StoresComponent},
      { path : 'maintenance/:market', component : MaintenanceComponent}
    ]},
    { path: 'associate',
    component: HomeComponent,
    canActivate:[AuthGuardService],
    children:[
      { path : '', redirectTo: 'associate/swagger', pathMatch: 'full'},
      { path : 'swagger', component: AssociateEndpointsComponent },
      { path : 'reprocess', component: AssociateReprocessComponent },
      { path : 'stores/:market', component: AssociateStoresComponent},
      { path : 'maintenance/:market', component : MaintenanceComponent}
    ]},
  { path: 'logout', component: HomeComponent },
  { path: '**', component: PageNotFoundComponent }
];


@NgModule({
  imports: [ RouterModule.forRoot(appRoutes, {useHash:true} )],
  exports: [ RouterModule ],
  declarations:[PageNotFoundComponent]
})
export class AppRoutingModule {}


