import { Component, OnInit } from '@angular/core';
import { AssociateReprocessService } from '../../../services/associate/associate-reprocess.service';

import { ServiceError } from '../../../models/service-error';
import { Message } from '../../../models/message';
import { Event } from '../../../models/event';
import { AssociateReprocessDto } from '../../../models/associate-reprocess-dto';
import { AssociateReprocessBuilder } from '../../../models/associate-reprocess-builder';
import { CustomObjectPipePipe } from '../../../directives/custom-object-pipe.pipe';
import { forEach } from '@angular/router/src/utils/collection';

@Component({
  selector: 'app-associate-reprocess',
  templateUrl: './associate-reprocess.component.html',
  styleUrls: ['./associate-reprocess.component.css']
})
export class AssociateReprocessComponent implements OnInit {

  public startDate: Date;
  public endDate: Date;
  public customObjectPipePipe: CustomObjectPipePipe;

  public events: Event[];
  public serviceError: ServiceError;
  public loadingRequest: boolean;
  public id: string = "";
  public win: number;
  public check: boolean;


  public filterQuery = "";
  public rowsOnPage = 5;
  public sortBy = "item.message.customObject.walmartIdentificationNumber";
  public sortOrder = "asc";
  
  
  constructor(public reprocessService: AssociateReprocessService) {
    this.customObjectPipePipe= new CustomObjectPipePipe();
  }

  ngOnInit() {
    this.events = [];
    this.serviceError = undefined;
    this.check = false;
    this.startDate= this.getToday();
    this.endDate = this.getToday();
  }

  private getToday():Date{
    let  date:Date=new Date();
    return new Date(date.getFullYear(),date.getMonth(),date.getDate());
  }
  
  public searchEvents() {
    this.events=[];
    this.serviceError=undefined;
    this.loadingRequest=true;
    this.reprocessService.getErrorEvents(this.id,this.startDate,this.endDate).subscribe(
      data =>{
        let events:any[] = data.filter((event)=>event.message.customObject!=null);
        if(events.length>0){
          this.events = events;
        }else{
          this.events =[];
          this.serviceError = new ServiceError("Events not found","Try with a different period of time");
        }
        this.loadingRequest = false
      },
      err => {
        console.debug(err);
        this.serviceError = err;
        this.loadingRequest = false;
      });
  }

  public reprocessEvents(){
    this.serviceError=undefined;
    this.loadingRequest=true;
    let dtos =[];
    this.events.forEach(event => {
      if(event.check){
      let dto = new AssociateReprocessBuilder().setEvent(event.id)
                  .setRpInstance(event.message.customObject.rpInstance)
                  .setWin(event.message.customObject.walmartIdentificationNumber)
                  .setLogId(event.message.customObject.associateLogId).build();
                  dtos.push(dto);
        }
    });

    this.reprocessService.reprocessEvents(dtos).subscribe(
      success=>{
        this.searchEvents();
      },
      error =>{
        console.debug(error);
        this.serviceError = error;
        this.loadingRequest = false;
      }
    );

  }

  public reprocessEvent(event : Event){
    this.serviceError=undefined;
    this.loadingRequest=true;

    let dto = new AssociateReprocessBuilder().setEvent(event.id)
                  .setRpInstance(event.message.customObject.rpInstance)
                  .setWin(event.message.customObject.walmartIdentificationNumber)
                  .setLogId(event.message.customObject.associateLogId).build();
                  
    this.reprocessService.reprocessEvent(dto).subscribe(
      success=>{
        this.searchEvents();
      },
      error =>{
        console.debug(error);
        this.serviceError = error;
        this.loadingRequest = false;
      }
    );
  }

  public selectAllEvents() {
    this.check=!this.check;
    for (let event of this.events) {
      event.check = this.check;
    }
  }

}



