import { Component, OnInit, OnDestroy} from '@angular/core';
import {BrowserModule, DomSanitizer, SafeResourceUrl} from '@angular/platform-browser'
import { ActivatedRoute } from '@angular/router';
import { DashboardEnum} from '../../models/dashboard-enum.enum';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit, OnDestroy{

  public topologyDashboardUrl:string;
  private sub: any;
  
  public kibanaUrl:SafeResourceUrl;
  
  constructor(private route: ActivatedRoute,
              private sanitizer: DomSanitizer) { }

  ngOnInit() {
    
    this.sub=this.route.params.subscribe(params => {
       let key:number= Number(DashboardEnum[params['view']]);
       switch (key) {
         case DashboardEnum.events:
           this.kibanaUrl=this.sanitizer.bypassSecurityTrustResourceUrl(environment.torsEventsDashboard);
           break;
        
        case DashboardEnum.topology:
          this.kibanaUrl=this.sanitizer.bypassSecurityTrustResourceUrl(environment.torsTopologyDashboard);
           break;
        
        case DashboardEnum.stores:
          this.kibanaUrl=this.sanitizer.bypassSecurityTrustResourceUrl(environment.torsStoresDashboard);
           break;
          case DashboardEnum.elastic:
           this.kibanaUrl=this.sanitizer.bypassSecurityTrustResourceUrl(environment.healthcheckElasticDashboard);
            break;
       
         default:
          this.kibanaUrl=this.sanitizer.bypassSecurityTrustResourceUrl(environment.torsTopologyDashboard);
           break;
       }
    });
    
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



