import { environment } from '../../../environments/environment';

export class TorEndpoints {

    constructor() { }
    
    private STORES:string='/stores';
    private STORE:string='/store';
    private MARKETS:string='/markets';
    private MARKET:string='/market';
    private TIME_OFF_REQUESTS:string='/timeOffRequests';
    private TIME_OFF_REQUEST:string='/timeOffRequest';
    private VALIDATION:string='/validation';
    private MAINTENANCE:string='/serverAvailability';
    private UPLOAD:string='/upload';
    private DELETE:string='/delete';
    private REPROCESS:string='/reprocess';
    private EVENTS:string='/events';
    private LIST:string='/list';

    getMarketsEndpoint():string{
        return environment.torContext+this.MARKETS;
    }

    loadStoresEndpoint():string{
        return environment.torContext+this.UPLOAD+this.STORES;
    }

    upsertStoreEndpoint():string{
        return environment.torContext+this.STORES+this.STORE;
    }

    getStoresEndpoint():string{
        return environment.torContext+this.STORES;
    }

    getStoresByMarketEndpoint(market:string):string{
        return environment.torContext+this.STORES+this.MARKET+'/'+market;
    }

    getStoreValidationByMarket(market:string,storeNumberGta:string):string{
        return environment.torContext+this.STORES+this.STORE+'/'+storeNumberGta+this.MARKET+'/'+market+this.VALIDATION;
    }

    getMaintenanceByMarket(market:string):string{
        return environment.torContext+this.MAINTENANCE+this.MARKET+'/'+market;    
    }

    upsertMaintenance():string{
        return environment.torContext+this.MAINTENANCE;
    }

    deleteMaintenance():string{
        return environment.torContext+this.MAINTENANCE+this.DELETE;
    }

    getEventsByIdOrDate(id:string, start:number,end:number):string{
        return environment.torContext+this.EVENTS;
    }

    reprocessTor():string{
        return environment.torContext+this.TIME_OFF_REQUESTS+this.TIME_OFF_REQUEST+this.REPROCESS;
    }

    reprocessTors():string{
        return environment.torContext+this.TIME_OFF_REQUESTS+this.TIME_OFF_REQUEST+this.REPROCESS+this.LIST;
    }

    deleteEvent(elasticId:string,elasticIndex:string):string{
        return environment.torContext+this.EVENTS+'/'+elasticIndex+'/'+elasticId;
    }
}



