import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpParams } from '@angular/common/http';
import { ApiRequestService } from '../api-request.service';
import {AssociateEnpoints } from './associate-enpoints';

import { Message } from '../../models/message';
import { Event } from '../../models/event';
import { AssociateReprocessDto } from '../../models/associate-reprocess-dto';


const EVENT_TYPE:string = "error";
const APP_NAME: string ="associate";

@Injectable()
export class AssociateReprocessService {

  constructor(private apiRequest: ApiRequestService, private endpoints: AssociateEnpoints) { };

  public getErrorEvents(id: string, startDate: Date, endDate: Date):Observable<Event[]>{

    let  DAY_CONSTANT = 3600*24*1000;
    let elasticId=(id==null || id==undefined)?"":id;
    let start = startDate.getTime();
    let end =endDate.getTime()+DAY_CONSTANT;
    let endpoint: string = this.endpoints.getEventsEndpoint(EVENT_TYPE,APP_NAME)
    let params = new HttpParams().set("id",id).set("startDate",start+"").set("endDate",end+"");

    return this.apiRequest.get(endpoint,params);
  }

  public reprocessEvents(events: AssociateReprocessDto[]):Observable<any>{
    let endpoint : string = this.endpoints.getReprocessEventsEndpoint();
    return this.apiRequest.post(endpoint, events);
  }

  public reprocessEvent(event: AssociateReprocessDto):Observable<any>{
    let endpoint : string = this.endpoints.getReprocessEventEndpoint();
    return this.apiRequest.post(endpoint, event);
  }


}



