import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, ViewChild, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs/Subject';
import { ServiceError } from '../../../models/service-error';
import { CalendarEvent, CalendarEventAction } from 'angular-calendar';
import { WeekDays } from '../../../models/week-days.enum'

import { MarketsService } from '../../../services/tor/markets.service';
import { MaintenanceService } from '../../../services/tor/maintenance.service';
import { MaintenanceDay, MaintenanceDayBuilder } from '../../../models/maintenance-builder';

import { Market } from '../../../models/market';
import { Maintenance } from '../../../models/maintenance';

@Component({
  selector: 'app-maintenance',
  templateUrl: './maintenance.component.html',
  styleUrls: ['./maintenance.component.css']
})
export class MaintenanceComponent implements OnInit, OnDestroy {

  private sub: any;

  
  public events = [];
  public startTime : Date;
  public endTime : Date ;
  public weekDays: string[];

  private mainntenances: Maintenance[];
  private market: string;
  private weekDay: string;

  public maintenanceErrorr: ServiceError;
  public loadingMaintenance: boolean;

  constructor(private route: ActivatedRoute,
              public maintenanceService: MaintenanceService) { }

  //Before job
  ngOnInit() {
    this.market = 'US';
    this.sub = this.route.params.subscribe(params => {
      this.market = params['market'];
      this.mainntenances = [];
      this.weekDays = this.maintenanceService.getWeekDays();
      this.weekDay = WeekDays[1];
      this.startTime = new Date();
      this.endTime = new Date();
      this.getMantenanceByMarket(this.market);
    });
  }

  //After job
  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  //Charge  calendar by market
  public getMantenanceByMarket(market: string) {
    this.mainntenances = [];
    this.market = market;
    this.events = [];
    this.maintenanceService.getMaintenanceByMarket(market)
      .subscribe(mainntenances => {
        this.mainntenances = mainntenances;
        this.maintenanceErrorr = null;
        this.events = this.maintenanceService.assignMaintenance(this.mainntenances, this.actions);
      },
      error => {
        this.maintenanceErrorr = error; this.loadingMaintenance = false;
      });
  }

  //Change day of week combo
  public changeDayOfWeek(weekDay: string) {
    this.weekDay = weekDay;
  }

  //Create new maintenance
  public addNewMaintenance() {
    this.loadingMaintenance = true;
    let maintenances: Maintenance[] = new MaintenanceDayBuilder().buildMaintenanceDayNew(this.startTime, this.endTime, this.market, this.weekDay);
    if (maintenances.length > 0) {
      this.maintenanceService.upsertMaintenance(maintenances[0]).subscribe(
        succes => {
          if (maintenances.length > 1) {
            this.maintenanceService.upsertMaintenance(maintenances[1]).subscribe(
              succes => {
                this.getMantenanceByMarket(this.market);
                this.maintenanceErrorr = null;
                this.loadingMaintenance = false;
              }, error => {
                this.maintenanceErrorr = error;
                this.loadingMaintenance = false;
              });
          }else{
            this.getMantenanceByMarket(this.market);
            this.maintenanceErrorr = null;
            this.loadingMaintenance = false;
          }
        }, error => {
          this.maintenanceErrorr = error;
          this.loadingMaintenance = false;
        });
    }else{
      this.maintenanceErrorr = this.maintenanceService.createError("Error creating maintenance","Data error");
      this.loadingMaintenance = false;
    }
  }

  //Delete  calendar event
  private actions: CalendarEventAction[] = [
    {
      label: '<i class="fa fa-fw fa-times"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.events = this.events.filter(iEvent => iEvent !== event);
        this.handleEvent('Deleted', event);
      }
    }
  ];

  //Event for delete  maintenance
  public handleEvent(action: string, event: any): void {
    this.maintenanceService.deletetMaintenance(new Maintenance(event.startHr, event.endHr, event.dayOfWeek, event.market)).subscribe(
      correct => { this.maintenanceErrorr = null; this.loadingMaintenance = false; },
      error => { this.maintenanceErrorr = error; this.loadingMaintenance = false; }
    );
  }

}


