import { format, addDays, addHours } from 'date-fns';
import { CalendarEvent, CalendarEventAction } from 'angular-calendar';

//models
import { Maintenance } from './maintenance';
import { WeekDays } from './week-days.enum';

export class MaintenanceDay {
    public startDate: Date;
    public endDate: Date;
    public startDateInit: number;
    public endDateInit: number;
    public dayOfWeek: string;
    public market: string;
    public maintenance: Maintenance;
    public event;
}

export interface IMaintenanceDayBuilder {
    setStartDate(market: string, startDate: number,event:number): void;
    setEndDate(market: string, startDate: number,event:number): void;
    setDayOfWeek(dayOfWeek: string): void;
    setMarket(market: string): void;
    setEvent(event: any): void;
    setMaintenance(): void;
    getResult(): MaintenanceDay;
}

export class MaintenanceDayBuilder implements IMaintenanceDayBuilder {
    private _maintenanceDay: MaintenanceDay;

    private static readonly dateFormatElastic = 'YYYY-MM-DDTHH:mm:ss';
    private static readonly colors: any = {
        red: {
            primary: '#ad2121',
            secondary: '#FAE3E3'
        },
        blue: {
            primary: '#1e90ff',
            secondary: '#D1E8FF'
        },
        yellow: {
            primary: '#e3bc08',
            secondary: '#FDF1BA'
        }
    };

    constructor() {
        this._maintenanceDay = new MaintenanceDay();
    }

    setStartDate(dayOfWeek: string, startHr: number,event:number): void {
        let today = new Date();
        this._maintenanceDay.startDate = addDays(today, this.getDiferenceDays(format(today, 'dddd'), dayOfWeek));
        let starttimeTemp = new String(startHr).split(".", 3);
        if(starttimeTemp.length>=2){
            if(event==0){
                starttimeTemp[1]=this.timeToOskiTLogic(starttimeTemp[1]);
            }
            if(starttimeTemp[1].length==1){
                starttimeTemp[1]=starttimeTemp[1]+"0";
            }
        }
        this._maintenanceDay.startDate.setHours(Number(starttimeTemp[0]), (starttimeTemp[1] == null || starttimeTemp[1] == undefined) ? 0 : Number((starttimeTemp[1].length == 1) ? starttimeTemp[1] + "0" : starttimeTemp[1]));
    }

    setEndDate(dayOfWeek: string, endHr: number,event:number): void {
        let today = new Date();
        this._maintenanceDay.endDate = addDays(today, this.getDiferenceDays(format(today, 'dddd'), dayOfWeek));
        let endtimeTemp = new String(endHr).split(".", 3);
        if(endtimeTemp.length>=2){
            if(event==0){
                endtimeTemp[1]=this.timeToOskiTLogic(endtimeTemp[1]);
            }
            if(endtimeTemp[1].length==1){
                endtimeTemp[1]=endtimeTemp[1]+"0";
            }
        }
        this._maintenanceDay.endDate.setHours(Number(endtimeTemp[0]), (endtimeTemp[1] == null || endtimeTemp[1] == undefined) ? 0 : Number((endtimeTemp[1].length == 1) ? endtimeTemp[1] + "0" : endtimeTemp[1]));
    }

    setDayOfWeek(dayOfWeek: string): void {
        this._maintenanceDay.dayOfWeek = dayOfWeek;
    }

    setMarket(market: string): void {
        this._maintenanceDay.market = market;
    }

    setEvent(a: any): void {
        let end = this.logicDateTodoubleDate(format(this._maintenanceDay.endDate, 'HH.mm'));
        let start = this.logicDateTodoubleDate(format(this._maintenanceDay.startDate, 'HH.mm'));
        this._maintenanceDay.event = {
            dayOfWeek: this._maintenanceDay.dayOfWeek,
            endHr: end,
            startHr: start,
            market: this._maintenanceDay.market,
            title: "[" + this.doubleToStringDate(start) + "-" + this.doubleToStringDate(end) + "]",
            start: this._maintenanceDay.startDate,
            end: this._maintenanceDay.endDate,
            color: MaintenanceDayBuilder.colors.red,
            draggable: true,
            actions: a,
            resizable: {
                beforeStart: true,
                afterEnd: true
            }
        };
    }

    setMaintenance(): void {
        let initDate = this.doubleDateToLogicDate(format(this._maintenanceDay.startDate, 'HH.mm'));
        let finalDate = this.doubleDateToLogicDate(format(this._maintenanceDay.endDate, 'HH.mm'));
        this._maintenanceDay.maintenance = new Maintenance(initDate, (finalDate == 0) ? 24 : finalDate, this._maintenanceDay.dayOfWeek, this._maintenanceDay.market);
    }

    initDateUtcToLocal(): void {
        let startDateLocal = this.convertUTCDateToLocalDate(this._maintenanceDay.startDate);
        if (Number(format(startDateLocal, 'd')) == 6 && Number(format(this._maintenanceDay.startDate, 'd')) == 0) {
            startDateLocal = addDays(startDateLocal, 7);
        }
        this._maintenanceDay.startDate = startDateLocal;
    }

    endDateUtcToLocal(): void {
        let endDateLocal = this.convertUTCDateToLocalDate(this._maintenanceDay.endDate);
        if (Number(format(endDateLocal, 'd')) == 6 && Number(format(this._maintenanceDay.endDate, 'd')) == 0) {
            endDateLocal = addDays(endDateLocal, 7);
        }
        this._maintenanceDay.endDate = endDateLocal;
    }

    initDateLocalToUtc(): void {
        this._maintenanceDay.startDate = this.convertLocalDateToUTCDate(this._maintenanceDay.startDate);
    }

    endDateLocalToUtc(): void {
        this._maintenanceDay.endDate = this.convertLocalDateToUTCDate(this._maintenanceDay.endDate);
    }

    getResult(): MaintenanceDay {
        return this._maintenanceDay;
    }

    private doubleDateToLogicDate(date: string): number {
        let stringDate = date.split(".", 3);
        let hour: number;
        let minutes: number = 0;
        if (stringDate.length > 0) {
            hour = Number(stringDate[0]);
            if (stringDate.length > 1) {
                minutes = Math.round(Number(stringDate[1]) * 100 / 60);
            }
        }
        let minuteString = (minutes < 10) ? "0" + minutes : new String(minutes);
        return Number(hour + "." + minuteString);
    }

    private doubleToStringDate(date:number):string{
        let starttimeTemp = new String(date).split(".", 3);
        let stringDate=starttimeTemp[0]+":"+((starttimeTemp[1]==null||starttimeTemp[1]==undefined)?"00":starttimeTemp[1]);
        return stringDate;
    }

    private timeToOskiTLogic(numb:string):string{
        let result:number=Math.round(Number(numb) * 60 / 100);
        return  result+"";
    }

    private logicDateTodoubleDate(date: string): number {
        let stringDate = date.split(".", 3);
        let hour: number;
        let minutes: number = 0;
        if (stringDate.length > 0) {
            hour = Number(stringDate[0]);
            if (stringDate.length > 1) {
                //minutes = Math.round(Number(stringDate[1]) * 60 / 100);
                minutes = Number(stringDate[1]);
            }
        }
        let minuteString = (minutes < 10) ? "0" + minutes : new String(minutes);
        return Number(hour + "." + minuteString)
    }

    private getDiferenceDays(today: string, day: string): number {
        return WeekDays[day] - WeekDays[today];
    }

    //utc date to local date
    private convertUTCDateToLocalDate(date): Date {
        var newDate = new Date(date.getTime() - date.getTimezoneOffset() * 60 * 1000);
        return newDate;
    }

    private modifyStartTime(start: number): void {
        this._maintenanceDay.maintenance.startHr = start;
    }

    private modifyEndTime(end: number): void {
        this._maintenanceDay.maintenance.endHr = end;
    }

    private duplicateBuilder(builder: MaintenanceDayBuilder): MaintenanceDayBuilder {
        let tempMaintenanceDayBuilder: MaintenanceDayBuilder = new MaintenanceDayBuilder();
        tempMaintenanceDayBuilder._maintenanceDay.maintenance = new Maintenance(builder._maintenanceDay.maintenance.startHr, builder._maintenanceDay.maintenance.endHr, builder._maintenanceDay.maintenance.dayOfWeek, builder._maintenanceDay.maintenance.market);
        return tempMaintenanceDayBuilder;
    }

    private addWeekDay(): void {
        let dayWeek = WeekDays[this._maintenanceDay.maintenance.dayOfWeek];
        this._maintenanceDay.maintenance.dayOfWeek = WeekDays[(dayWeek == 7) ? 1 : dayWeek + 1];
    }

    private componentToDate(dayWeek: string, date): Date {
        return this.numberToDate(dayWeek, Number(date.hour + "." + date.minute));
    }

    private numberToDate(dayOfWeek: string, startHr: number): Date {
        let today = new Date();
        let tempDate = addDays(today, this.getDiferenceDays(format(today, 'dddd'), dayOfWeek));
        let starttimeTemp = new String(startHr).split(".", 3);
        tempDate.setHours(Number(starttimeTemp[0]), (starttimeTemp[1] == null || starttimeTemp[1] == undefined) ? 0 : Number(starttimeTemp[1]));
        return tempDate;
    }

    //local date to utc date
    private convertLocalDateToUTCDate(date) {
        const dateWithNoTimezone = new Date(
            date.getUTCFullYear(),
            date.getUTCMonth(),
            date.getUTCDate(),
            date.getUTCHours(),
            date.getUTCMinutes(),
            date.getUTCSeconds()
        );
        return dateWithNoTimezone;
    }

    //maintenance to  MaintenanceDay
    public buildMaintenanceDayView(maintenance: Maintenance, actions: CalendarEventAction[]): MaintenanceDay {
        this.setMarket(maintenance.market);
        this.setStartDate(maintenance.dayOfWeek, maintenance.startHr,0);
        this.setEndDate(maintenance.dayOfWeek, maintenance.endHr,0);
        this.setDayOfWeek(maintenance.dayOfWeek);
        this.initDateUtcToLocal();
        this.endDateUtcToLocal();
        this.setEvent(actions);
        return this.getResult();
    }

    public buildMaintenanceDayDelete(maintenance: Maintenance): MaintenanceDay {
        this.setMarket(maintenance.market);
        this.setStartDate(maintenance.dayOfWeek, maintenance.startHr,1);
        this.setEndDate(maintenance.dayOfWeek, maintenance.endHr,1);
        this.setDayOfWeek(maintenance.dayOfWeek);
        this.initDateLocalToUtc();
        this.endDateLocalToUtc();
        this.setMaintenance();
        return this.getResult();
    }

    public buildMaintenanceDayNew(initDate:Date, endDate:Date, market: string, weekDay: string): Maintenance[] {
        let maintenanceDay: Maintenance[] = [];
        let maintenanceBuilder = new MaintenanceDayBuilder();
        maintenanceBuilder.setMarket(market);
        maintenanceBuilder.setStartDate(weekDay, Number(initDate.getHours() + "." + initDate.getMinutes()),1);
        maintenanceBuilder.setEndDate(weekDay, Number(endDate.getHours() + "." + endDate.getMinutes()),1);
        maintenanceBuilder._maintenanceDay.startDateInit=maintenanceBuilder._maintenanceDay.startDate.getHours();
        maintenanceBuilder._maintenanceDay.endDateInit=maintenanceBuilder._maintenanceDay.endDate.getHours();
        if (maintenanceBuilder._maintenanceDay.startDate.getHours() <= maintenanceBuilder._maintenanceDay.endDate.getHours()) {
            if (maintenanceBuilder._maintenanceDay.startDate.getHours() == maintenanceBuilder._maintenanceDay.endDate.getHours() && maintenanceBuilder._maintenanceDay.startDate.getMinutes() >= maintenanceBuilder._maintenanceDay.endDate.getMinutes()) {
            } else {
                console.log("first validation");
                maintenanceBuilder.setDayOfWeek(weekDay);
                maintenanceBuilder.initDateLocalToUtc();
                maintenanceBuilder.endDateLocalToUtc();
                maintenanceBuilder.setMaintenance();
                if (maintenanceBuilder._maintenanceDay.startDate.getHours() > maintenanceBuilder._maintenanceDay.endDate.getHours()) {
                    //split save 
                    console.log("split save");
                    let temporalMaintenance = this.duplicateBuilder(maintenanceBuilder);
                    maintenanceBuilder.modifyEndTime(24.0);
                    temporalMaintenance.modifyStartTime(0.0);
                    temporalMaintenance.addWeekDay();
                    maintenanceDay.push(maintenanceBuilder._maintenanceDay.maintenance);
                    maintenanceDay.push(temporalMaintenance._maintenanceDay.maintenance);
                } else {
                    if (this.componentToDate(weekDay, initDate).getHours() >= maintenanceBuilder._maintenanceDay.startDate.getHours()) {
                        //regular save , nex day
                        console.log("regular save next day");
                        maintenanceBuilder.addWeekDay();
                        maintenanceDay.push(maintenanceBuilder._maintenanceDay.maintenance);
                    } else {
                        //regular save 
                        if(maintenanceBuilder._maintenanceDay.startDate.getHours()<maintenanceBuilder._maintenanceDay.startDateInit){
                            maintenanceBuilder.addWeekDay();
                        }
                        maintenanceDay.push(maintenanceBuilder._maintenanceDay.maintenance);
                    }
                }
            }
        }
        return maintenanceDay;
    }
}



