import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { WfmTableDirective } from '../../../directives/wfm-table.directive';
import { WfmPaginator } from '../../../directives/paginator';
import { DataFilterPipe } from '../../../directives/data-filter.pipe';


import { StoresService } from '../../../services/tor/stores.service';
import { MarketsService } from '../../../services/tor/markets.service';
import { TorStore } from '../../../models/tor-store';
import { Market } from '../../../models/market';
import { TimeOffRequestValidation } from '../../../models/time-off-request-validation';
import { ServiceError } from '../../../models/service-error';


import { Json2Csv } from '../../../commons/json-2-csv';
import { PapaParseService } from 'ngx-papaparse';


enum ViewEnum {
  add = 1,
  update = 2,
  load = 3,
}


@Component({
  selector: 'app-stores',
  templateUrl: './stores.component.html',
  styleUrls: ['./stores.component.css']
})
export class StoresComponent implements OnInit, OnDestroy {

  private sub: any;

  public markets: Market[];
  public stores: TorStore[];
  public currentStore: TorStore;
  public market:string;

  public showEdit: boolean;
  public viewId: number;

  public storesError: ServiceError;
  public loadingStores: boolean;

  public closeResult: string;
  public filterQuery = "";
  public rowsOnPage = 5;
  public sortBy = "storeNumber";
  public sortOrder = "asc";

  constructor(private route: ActivatedRoute,
    public storesServices: StoresService,
    public marketsService: MarketsService,
    public papa: PapaParseService
  ) { }

  ngOnInit() {
  
    this.sub = this.route.params.subscribe(params => {
      this.market = params['market']
      this.viewId = 0;
      this.stores = [];
      this.markets = [];
      this.showEdit = false;
      this.loadStores(this.market);
      this.filterQuery="";
      this.marketsService.getMarkets().subscribe(
        markets => { this.markets = markets; this.loadingStores = false; },
        error => { this.storesError = error; this.loadingStores = false; });
    });

  }

  public loadStores(market: string) {
    this.storesError=undefined;
    this.loadingStores = true;
    this.storesServices.getStoresByMarket(market).subscribe(
      stores => { this.stores = stores; this.loadingStores = false; },
      error => { this.storesError = error; this.loadingStores = false; }
    );
  }

  public downloadStoresInScope() {
    let date: Date = new Date();
    new Json2Csv(this.papa.unparse(this.stores), 'storesInScope-' + date.getFullYear() + (date.getMonth() + 1) + date.getDate());
  }


  public openEdit(action: string, store: TorStore) {
    this.storesError=undefined;
    this.showEdit = true;
    this.viewId = ViewEnum[action];
    if (store == undefined) {
      this.currentStore = new TorStore();
      this.currentStore.market = this.market;
    } else {
      this.currentStore = store;
    }

  }

  public closeEdit() {
    this.currentStore = undefined;
    this.showEdit = false;
    this.storesError=undefined;
  }

  public upsertStore() {
    this.loadingStores = true;
    this.storesError=undefined;
    this.storesServices.upsertStore(this.currentStore).subscribe(
      success => {
        this.loadStores(this.currentStore.market);
        this.closeEdit();
      },
      error => {
        { this.storesError = error; this.loadingStores = false; }
      }
    );
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



