import { Component, OnInit } from '@angular/core';
import { Message } from '../../../models/message';
import { Event } from '../../../models/event';
import { TorService } from '../../../services/tor/tor.service';
import { ServiceError } from '../../../models/service-error';
import { CustomObjectPipePipe } from '../../../directives/custom-object-pipe.pipe';

@Component({
  selector: 'app-tors',
  templateUrl: './tors.component.html',
  styleUrls: ['./tors.component.css']
})
export class TorsComponent implements OnInit {

  startDate: Date;
  endDate: Date;


  public events: Event[];
  public torsError: ServiceError;
  public loadingTors: boolean;
  public id: string = "";
  public win: number;
  public check: boolean;


  public filterQuery = "";
  public rowsOnPage = 5;
  public sortBy = "item.message.customObject.winNumber";
  public sortOrder = "asc";

  private customObjectPipePipe : CustomObjectPipePipe;

  constructor(public torService: TorService) {
    this.customObjectPipePipe= new CustomObjectPipePipe();
  }

  ngOnInit() {
    this.events = [];
    this.torsError = undefined;
    this.check = false;
    this.startDate= this.getToday();
    this.endDate = this.getToday();
  }

  private getToday():Date{
    let  date:Date=new Date();
    return new Date(date.getFullYear(),date.getMonth(),date.getDate());
  }

  public searchEvents() {
    this.events = [];
    this.loadingTors = true;
    this.torsError = undefined;
    this.torService.getEventsByIdAndDate(this.id, this.startDate, this.endDate).subscribe(
      data =>{
        let events:any[] = data.filter((event)=>event.message.customObject!=null);
        if(events.length>0){
          this.events = events;
        }else{
          this.events =[];
          this.torsError = new ServiceError("Events not found","Try with a different period of time");
        }
        this.loadingTors = false
      },
      err => {
        console.debug(err);
        this.torsError = err;
        this.loadingTors = false;
      });
  }


  public reprocessTor(event: Event) {
    this.loadingTors = true;
    this.torService.reprocesTor(event).subscribe(correct => {
      this.torService.deleteEvent(event).subscribe(
        correct => { this.filterQuery="" ;setTimeout(() => this.searchEvents(), 500); },
        error => { this.torsError = error; this.loadingTors = false; });
    }, error => { this.torsError = error; this.loadingTors = false; });
  }

  public reprocessTors() {
    this.loadingTors=true;
    let events= this.customObjectPipePipe.transform(this.events,this.filterQuery);
    let eventList: string[] = [];
    for (let event of events) {
      if (event.check) {
        eventList.push(event.id);
      }
    }
    this.torService.reprocesTors(eventList).subscribe(
      correct => {this.check=false; this.filterQuery="" ;setTimeout(() => this.searchEvents(), 900); },
      error => { this.torsError = error; this.loadingTors = false; });
  }

  public selectAllEvents() {
    this.check=!this.check;
    for (let event of this.events) {
      event.check = this.check;
    }
  }

}


