import {AssociateReprocessDto} from './associate-reprocess-dto'

export class AssociateReprocessBuilder {
    /*public win :string;
	public logId : number;
	public rpInstance : string;
	public event : string;*/

    private _reprocessDto: AssociateReprocessDto;

    constructor(){
        this._reprocessDto= new AssociateReprocessDto();
    }

    public setRpInstance(rpInstance:string): AssociateReprocessBuilder{
        this._reprocessDto.rpInstance= rpInstance;
        return this;
    }

    public setLogId(logId:number): AssociateReprocessBuilder{
        this._reprocessDto.logId=logId;
        return this;
    }

    public setWin(win:string) : AssociateReprocessBuilder{
        this._reprocessDto.win = win;
        return this;
    }

    public setEvent(event:string) : AssociateReprocessBuilder{
        this._reprocessDto.event = event;
        return this;
    }

    public build():AssociateReprocessDto{
        return this._reprocessDto;
    }

}



