import { Component, OnInit } from '@angular/core';
import { CarouselConfig } from 'ngx-bootstrap/carousel';
import {DomSanitizer, SafeResourceUrl} from '@angular/platform-browser'
import { RouterModule, Router } from '@angular/router';
import { MenuService } from '../../services/api/menu.service';
import { LoginService } from '../../services/api/login.service';
import { UserInfoService } from '../../services/api/user-info.service';

import { Market } from '../../models/market';
import { ServiceError } from '../../models/service-error';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css'],
  providers: [
    { provide: CarouselConfig, useValue: { interval: 1500, noPause: true, showIndicators: true } }
  ]
})
export class NavComponent implements OnInit {

  public menus: any[];
  public resetMenu: string;
  public markets: Market[];

  public serviceError : ServiceError;
  public userId : string;

  constructor(private router: Router,
              private menuService: MenuService,
              private loginService:LoginService, 
              private userService:UserInfoService,
              private sanitizer: DomSanitizer){}

  ngOnInit() {
    console.info("nav component init")
    this.serviceError = undefined;
    let currentPath=this.router.url;
    this.menuService.getUserMenu().subscribe(
      data=> {this.resetMenu = JSON.stringify(data);
              this.refreshCurrentActive(currentPath);},
      error => {console.error(error); this.serviceError = error}
    );
    this.userId = this.userService.getUserId();
  }

  public logout (){
    this.loginService.logout();
    let logoutUrl: string =environment.ssoLogoutUrl+"?post_logout_redirect_uri="+environment.hostUrl;
    window.open(logoutUrl,"_self");
  }

  public login(){
    let today = new Date();
    let redirect = "&redirect_uri="+environment.hostUrl;
    let nonce ="&nonce="+today.getFullYear()+today.getMonth()+today.getDate();
    let state ="&state="+today.getFullYear()+today.getMonth()+today.getDate();

    let loginUrl : string = environment.ssoLoginUrl+redirect+nonce+state;
    window.open(loginUrl,"_self");
  }

  public refreshCurrentActive( currentPath: string) {
    //restart the menu
        this.menus = JSON.parse(this.resetMenu);
        let paths = currentPath.split("/");
        let selectedMenu: any;
        for (var index in paths) {

          let level: number = Number(index);
          switch (level) {
            case 1:
              this.menus.forEach(menu => {
                if (menu.path == paths[level]) {
                  menu.selected = true;
                  selectedMenu = menu;
                }
              });
              break;
            case 2:
              selectedMenu.children.forEach(submenu => {
                if (submenu.path == paths[level]) {
                  submenu.selected = true;
                  selectedMenu = submenu;
                }
              });
              break;
            case 3:
              selectedMenu.children.forEach(submenu => {
                if (submenu.path == paths[level]) {
                  submenu.selected = true;
                }
              });
              break;
            default:
              break;
          }
    }
       
    }
  }



