import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse} from '@angular/common/http';
import { Observable} from 'rxjs';
import { ApiRequestService } from '../api-request.service';
import { TorEndpoints } from './tor-endpoints'
import { TorStore } from '../../models/tor-store';
import { ServiceError } from '../../models/service-error';

@Injectable()
export class StoresService {

  constructor(private apiRequest: ApiRequestService, private endpoints: TorEndpoints) {
    
  }

   public getStores(): Observable<TorStore[]>{
      return this.apiRequest.get(this.endpoints.getStoresEndpoint());
    }

    public getStoresByMarket(market :string): Observable<TorStore[]>{
      return this.apiRequest.get(this.endpoints.getStoresByMarketEndpoint(market));
    }

    public loadStores(file:File): Observable<any>{
      let formData: FormData = new FormData(); 
      formData.append('file',file); 
      return this.apiRequest.post(this.endpoints.loadStoresEndpoint(),formData);
    }

   public upsertStore(store:TorStore): Observable<TorStore>{
      return this.apiRequest.put(this.endpoints.upsertStoreEndpoint(),store);
    }

    public  validateStore(store:TorStore): Observable<TorStore>{
      return this.apiRequest.get(this.endpoints.getStoreValidationByMarket(store.market,store.storeNumberGta));
    }
}



