import { environment } from '../../../environments/environment';


const BASE           : string = "/api/v1";
const SWAGGER        : string = "/swagger.json";
const RP_INSTANCES   : string = "/rpInstances";
const RP_INSTANCE    : string = "/rpInstance"
const EVENTS         : string ="/events"
const EVENT          : string ="/event"
const EVENT_TYPE     : string = "/eventType";
const APP_NAME       : string = "/appName";
const REPROCESS      : string = "/reprocess";
const STORES         : string = "/stores";
const STORE          : string = "/store";
const PILOT_STORES   : string ="/pilotTypes";

export class AssociateEnpoints {

    constructor() { }

    public getSwaggerJsonEndpoint():string{
        return environment.associateContext+BASE+SWAGGER; 
    }

    public getRpInstancesEndpoint():string{
        return environment.associateContext+BASE+RP_INSTANCES;
    }

    public getEventsEndpoint(type:string,appName:string):string{
        return environment.associateContext+BASE+EVENTS+EVENT_TYPE+'/'+type+APP_NAME+'/'+appName;
    }

    public getReprocessEventsEndpoint():string{
        return environment.associateContext+BASE+REPROCESS+EVENTS;
    }

    public getReprocessEventEndpoint():string{
        return environment.associateContext+BASE+REPROCESS+EVENT;
    }

    public getAssociateStoresEndpoint(rpInstance:string):string{
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+'/'+STORES;        
    }

    public getStoresByRpInstanceEndpoint(rpInstance:string):string{
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+STORES;
    }

    public getStoreInfoEndpoint(rpInstance:string,storeNumber:number):string{
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+STORES+STORE+'/'+storeNumber;
    }

    public getPilotTypesByStoreEndpoint(rpInstance:string,storeNumber:number):string{
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+STORES+'/'+storeNumber+PILOT_STORES;
    }

    public createStoreEndpoint(rpInstance:string){
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+STORES;
    }

    public updateStoreEndpoint(rpInstance:string, storeNumber:number){
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+STORES+'/'+storeNumber;
    }

    public updateStorePilotType(rpInstance:string){
        return environment.associateContext+BASE+RP_INSTANCES+'/'+rpInstance+STORES;
    }

}



